.vux-step {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.vux-step-item {
  display: inline-block;
  position: relative;
  overflow: hidden;
}
.vux-step-item-with-tail {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.vux-step-item-tail {
  height: 1px;
  position: absolute;
  left: 0;
  top: 12px;
  padding: 0 0;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}
.vux-step-item-tail-finish {
  background: #09bb07 none repeat scroll 0 0;
}
.vux-step-item-tail-process,
.vux-step-item-tail-wait {
  background: #CCC none repeat scroll 0 0;
}
.vux-step-item-icon {
  width: 22px;
  height: 22px;
  display: inline-block;
  text-align: center;
}
.vux-step-item-checked::before {
  font-size: 15px!important;
  line-height: 22px;
  margin: 0!important;
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
}
.vux-step-item-title {
  font-size: 0.8rem;
}
.vux-step-item-head {
  position: relative;
  display: inline-block;
  margin-right: -4px;
}
.vux-step-item-head .vux-step-item-head-inner {
  width: 22px;
  height: 22px;
  line-height: 22px;
  border-radius: 99px;
  text-align: center;
  font-size: 14px;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
  background: #fff none repeat scroll 0 0;
}
.vux-step-item-head-finish .vux-step-item-head-inner {
  border: 1px solid #09bb07;
  color: #09bb07;
}
.vux-step-item-head-process .vux-step-item-head-inner {
  border: 1px solid #09bb07;
  color: #FFF;
  background: #09bb07 none repeat scroll 0 0;
}
.vux-step-item-head-wait .vux-step-item-head-inner {
  border: 1px solid #888;
  color: #888;
}
.vux-step-item-main {
  display: inline-block;
  position: relative;
  vertical-align: top;
  color: #888;
  padding-left: 5px;
}
.vux-step-item-main-process {
  font-weight: bold;
  color: #666;
}

.vux-x-hr {
  padding-top: 15px;
  margin-bottom: 15px;
  position: relative;
}
.vux-x-hr:before {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}

.vux-fade-enter-active,
.vux-fade-leave-active {
  opacity: 1;
  -webkit-transition: opacity linear 0.2s;
  transition: opacity linear 0.2s;
}
.vux-fade-enter,
.vux-fade-leave-to {
  opacity: 0;
}
.vux-dialog-enter-active {
  -webkit-animation: vux-dialog-in 0.5s;
          animation: vux-dialog-in 0.5s;
}
.vux-dialog-leave-active {
  -webkit-animation: vux-dialog-out 0.3s;
          animation: vux-dialog-out 0.3s;
}
@-webkit-keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
}
@keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
}
.vux-mask-enter,
.vux-mask-leave-active {
  opacity: 0;
}
.vux-mask-leave-active,
.vux-mask-enter-active {
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
